import { createSlice } from '@reduxjs/toolkit'

export const mainSlice = createSlice({
  name: 'mainPage',
  initialState: {
    mainCards: [
      { id: 'card0', title: '   ->', price: '5540 .', img: 'card0', action: '' },
      { id: 'card1', title: '   ->', price: '4540 .', img: 'card1', action: '' },
      { id: 'card2', title: ' ', price: '3540 .', img: 'card2', action: '' },
    ],
    interestCards: [
      { id: 'card0', title: ' !', text: '   ', price: '2500 .', img: 'card3', action: '' },
      { id: 'card1', title: '  1', price: '2500 .', img: 'card4', action: '' },
      { id: 'card2', title: '', price: '2500 .', img: 'card5', action: '' },
    ],
    popularProducts: [
      {title: '', price: '3540 .', img: 'p0'},
      {title: '  ', price: '3540 .', img: 'p1'},
      {title: ' ', price: '3540 .', img: 'p2'},
      {title: ' ', price: '3540 .', img: 'p3'},
      {title: ' ', price: '3540 .', img: 'p4'},
      {title: ' ', price: '3540 .', img: 'p2'},
    ],
    testimonials: [
      {text: '  ,  .   .   .  . ', author: ' ', img: 'p5'},
      {text: '  ,  .   .   .  . ', author: ' ', img: 'p5'},
      {text: '  ,  .   .   .  . ', author: ' ', img: 'p5'},
      {text: '  ,  .   .   .  . ', author: ' ', img: 'p5'},
      {text: '  ,  .   .   .  . ', author: ' ', img: 'p5'},
      {text: '  ,  .   .   .  . ', author: ' ', img: 'p5'},
    ]
  },
  reducers: {
    increment: (state) => {
      state.value += 1
    },
    decrement: (state) => {
      state.value -= 1
    },
    incrementByAmount: (state, action) => {
      state.value += action.payload
    },
  },
})

export const { increment, decrement, incrementByAmount } = mainSlice.actions

export default mainSlice.reducer
