import React from 'react'
import s from "./RegistrForm.module.css";
import { useFormik } from "formik";

const validate = values => {
  const errors = {};
  if (!values.fullName) { errors.fullName = 'Required'; }
  if (!values.phone) { errors.phone = 'Required'; }
  if (!values.email) { errors.email = 'Required'; }
  if (!values.password) { errors.password = 'Required'; }
  if (!values.code) { errors.code = 'Required'; }
  if (!values.confirm) { errors.confirm = 'Required'; }
  return errors;
};

function RegistrForm({ handleForm }) {
  const formik = useFormik({
    initialValues: {
      fullName: '',
      phone: '',
      email: '',
      password: '',
      code: '',
      codeby: '',
      subscribe: '',
      confirm: '',
    },
    validate,
    onSubmit: (values) => {
      console.log(values);
      formik.resetForm();
    },
  });
  return (
    <div>
      <div className={s.title}>
        <p></p>
        <p onClick={() => { handleForm("login"); }} ></p>
      </div>
      <div className={s.registrForm}>
        <form onSubmit={formik.handleSubmit}>
          <div className={s.formControl}>
            <label htmlFor="fullName">/</label>
            <input
              id="fullName"
              placeholder="/"
              name="fullName"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.fullName}
            />
            {formik.touched.fullName && formik.errors.fullName ? 
              <small>{formik.errors.fullName}</small> : null}
          </div>

          <div className={s.formControl}>
            <label htmlFor="phone"> </label>
            <input
              id="phone"
              placeholder=" "
              name="phone"
              type="tel"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.phone}
            />
            {formik.touched.phone && formik.errors.phone ? 
              <small>{formik.errors.phone}</small> : null}
          </div>

          <div className={s.formControl}>
            <label htmlFor="email">E-mail</label>
            <input
              id="email"
              placeholder="E-mail"
              name="email"
              type="email"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.email}
            />
            {formik.touched.email && formik.errors.email ? 
              <small>{formik.errors.email}</small> : null}
          </div>

          <div className={s.formControl}>
            <label htmlFor="password"></label>
            <input
              id="password"
              placeholder=""
              name="password"
              type="password"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.password}
            />
            {formik.touched.password && formik.errors.password ? 
              <small>{formik.errors.password}</small> : null}
          </div>
          <div className={s.formControl}>
            <label> </label>
          </div>
          <div className={s.getCode}>
            <label>
              <input type="radio" name="codeby" value="byphone" onChange={formik.handleChange} />
              
            </label>
            <label>
              <input type="radio" name="codeby" value="byemail" onChange={formik.handleChange} />
              E-mail
            </label>
            <label style={{color: '#962689'}}></label>
          </div>

          <div className={s.formControl}>
            <label htmlFor="code"> </label>
            <input
              id="code"
              placeholder=" "
              name="code"
              type="text"
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              value={formik.values.code}
            />
            {formik.touched.code && formik.errors.code ? 
              <small>{formik.errors.code}</small> : null}
            <br /><small style={{color: '#962689'}}>  </small> 
          </div>

          <div className={s.checboxes}>
            <input
              id="subscribe"
              name="subscribe"
              type="checkbox"
              onChange={formik.handleChange}
              value={formik.values.subscribe}
            />
            <label htmlFor="subscribe">      </label>
          </div>
          <div className={s.checboxes}>
            <input
              id="confirm"
              name="confirm"
              type="checkbox"
              onChange={formik.handleChange}
              value={formik.values.confirm}
            />
            <label htmlFor="confirm"
              style={formik.touched.confirm && formik.errors.confirm ? {color: 'red'} : null}>    </label>
          </div>

          <button type="submit" className={s.formBtn}></button>
        </form>
      </div>
    </div>
  );
}

export default RegistrForm
