import { createSlice } from '@reduxjs/toolkit'

export const productsSlice = createSlice({
  name: 'productsPage',
  initialState: {
    products: [
      { title: "", price: "3540 .", img: "p0" },
      { title: "  ", price: "3540 .", img: "p1" },
      { title: " ", price: "3540 .", img: "p2" },
      { title: " ", price: "3540 .", img: "p3" },
      { title: " ", price: "3540 .", img: "p4" },
      { title: " ", price: "3540 .", img: "p2" },
      { title: "  ", price: "3540 .", img: "p1" },
      { title: " ", price: "3540 .", img: "p2" },
      { title: " ", price: "3540 .", img: "p3" },
      { title: " ", price: "3540 .", img: "p4" },
      { title: " ", price: "3540 .", img: "p2" },
    ],
  },
  reducers: {
    increment: (state) => {
      state.value += 1
    },
    decrement: (state) => {
      state.value -= 1
    },
  },
})

export const { increment, decrement } = productsSlice.actions

export default productsSlice.reducer
